.custiv {
  width: 100%;
}

.custivCompany {
    background-color: #2A2B3B;
    padding-left: 10%;
    padding-right: 10%;
    padding-top: 40px;
  }
  
  .custivCompanyTop {
    display: flex;
    justify-content: flex-start;
  }
  
  .custivCompanyTopImg {
    width: 90px !important;
    height: 90px;
    background-color: white;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border-radius: 8px;
  }
  
  .custivCompanyTopImg > img {
    width: 80%!important;
    height: 80% !important;
  }
  
  .custivCompanyTopRight {
    display: flex;
    flex-direction: column;
    margin-left: 40px;
  }
  
  .custivCompanyTopRight > h1 {
    font-size: 35px;
    line-height: 1.2em;
    letter-spacing: .05em;
    color: white;
    font-family: display-Bold,sans-serif;
    width: 70%;
  }
  
  .custivCompanyTopRight > p {
    line-height: 1.5em!important;
    font-size: 22px!important;
    letter-spacing: .05em!important;
    font-weight: 400!important;
    font-family: display-Light,sans-serif!important;
    max-width: 48rem!important;
    margin-top: 0.5rem!important;
    color: white;
  }

  .custivCompanyMiddle {
    display: flex;
    margin-top: 40px;
  }
  
  .custivCompanyMiddle > h5 {
    width: 7rem!important;
    height: 1.5rem!important;
    margin-right: 0.5rem!important;
    color: rgba(255,255,255,.7)!important;
    line-height: inherit;
    letter-spacing: .05em;
    font-family: display-Regular,sans-serif;
  }
  
  .custivCompanyMiddle > p {
    flex: 1 1 0%!important;
    height: 100%!important;
    color: rgba(255,255,255,.7)!important;
    line-height: inherit;
    letter-spacing: .05em;
    font-family: display-Regular,sans-serif;
    margin-left: 25px;
  }
  
  .custivCompanyBottom {
    display: flex;
    margin-top: 40px;
  }
  
  .custivCompanyBottom > h5 {
    width: 7rem!important;
    height: 1.5rem!important;
    margin-right: 0.5rem!important;
    color: rgba(255,255,255,.7)!important;
